/*
 Calculates a size of text elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-text-size` custom prop.
 */
/*
 Calculates a size of UI elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-ui-size` custom prop.
 */
.alpheios-navmenu {
  background: #FFF;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
}

.alpheios-navmenu__item {
  display: flex;
  padding: 10px 20px 10px 30px;
  border-bottom: 1px solid var(--alpheios-color-neutral-light);
  cursor: pointer;
}
.alpheios-navmenu__item:hover, .alpheios-navmenu__item:focus {
  color: var(--alpheios-color-vivid-hover);
}
.alpheios-navmenu__item.active {
  color: var(--alpheios-color-vivid);
}
.alpheios-navmenu__item .alpheios-navbuttons__icon, .alpheios-navmenu__item:hover .alpheios-navbuttons__icon, .alpheios-navmenu__item.active:focus .alpheios-navbuttons__icon {
  fill: var(--alpheios-color-vivid);
  stroke: var(--alpheios-color-vivid);
}
.alpheios-navmenu__item:hover .alpheios-navbuttons__icon, .alpheios-navmenu__item:focus .alpheios-navbuttons__icon, .alpheios-navmenu__item.active .alpheios-navbuttons__icon {
  fill: var(--alpheios-color-vivid-hover);
  stroke: var(--alpheios-color-vivid-hover);
}

.alpheios-navbuttons__icon-cont {
  flex-grow: 0;
  width: 40px;
  height: 40px;
  margin-right: 40px;
}

.alpheios-navbuttons__icon {
  width: 40px;
  height: 40px;
}

.alpheios-navmenu div.alpheios-navmenu__text {
  font-size: 20px;
  line-height: 1.2;
  top: 10px;
  position: relative;
}