/*
 Calculates a size of text elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-text-size` custom prop.
 */
/*
 Calculates a size of UI elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-ui-size` custom prop.
 */
.alpheios-word-usage {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.alpheios-word-usage div.alpheios_word_usage_list_title {
  flex: none;
  font-weight: bold;
  padding-bottom: 5px;
  border-bottom: 1px solid var(--alpheios-border-color);
  margin-bottom: 10px;
}
.alpheios-word-usage div.alpheios_word_usage_list_mainblock {
  flex: 1 1 auto;
  position: relative;
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
}
.alpheios-word-usage div.alpheios-word_usage_list__provider {
  flex: none;
  font-weight: normal;
  padding: 10px 0;
  font-size: 80%;
}
.alpheios-word-usage .alpheios-word-usage-header {
  border-bottom: 1px solid var(--alpheios-border-color);
}
.alpheios-word-usage .alpheios-word-usage-header-select-author,
.alpheios-word-usage .alpheios-word-usage-header-select-textwork,
.alpheios-word-usage .alpheios-word-usage-header-select-sortBy {
  width: 88%;
  max-width: 400px;
}
.alpheios-word-usage .alpheios-select:not([multiple]):not([size]) {
  background-size: 10%;
}
.alpheios-word-usage .alpheios-word-usage-header-clear-icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  cursor: pointer;
  vertical-align: middle;
}
.alpheios-word-usage .alpheios-word-usage-header-clear-icon svg {
  width: 100%;
  height: 100%;
}
.alpheios-word-usage .alpheios-word-usage-header-clear-disabled.alpheios-word-usage-header-clear-icon {
  cursor: inherit;
  fill: var(--alpheios-text-color);
}
.alpheios-word-usage__examples {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-auto-rows: auto;
}
.alpheios-word-usage__examples-show-sources-btn {
  margin: 40px 0 20px;
}
.alpheios-panel--large .alpheios-word-usage__examples-show-sources-btn {
  display: none;
}
.alpheios-word-usage a.alpheios-word-usage__examples-source-link-large {
  grid-column: 1/4;
  color: var(--alpheios-link-color-on-light);
  padding-top: 10px;
  padding-bottom: 5px;
}
.alpheios-word-usage a.alpheios-word-usage__examples-source-link-compact {
  grid-column: 1/4;
  color: var(--alpheios-link-color-on-light);
  padding-top: 5px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--alpheios-border-color);
}
.alpheios-word-usage__examples-pre {
  grid-column: 1;
  text-align: right;
  border-bottom: 1px solid var(--alpheios-border-color);
}
.alpheios-word-usage__examples-target-word {
  grid-column: 2;
  text-align: center;
  padding: 0 3px;
  color: var(--alpheios-highlight-dark-color);
  font-weight: 700;
  border-bottom: 1px solid var(--alpheios-border-color);
}
.alpheios-word-usage__examples-post {
  grid-column: 3;
  text-align: left;
  border-bottom: 1px solid var(--alpheios-border-color);
}
.alpheios-word-usage__examples-pre, .alpheios-word-usage__examples-target-word, .alpheios-word-usage__examples-post {
  padding-bottom: 10px;
  border-bottom: 1px solid var(--alpheios-border-color);
  white-space: nowrap;
}
.alpheios-word-usage__examples--sources-visible .alpheios-word-usage__examples-pre, .alpheios-word-usage__examples--sources-visible .alpheios-word-usage__examples-target-word, .alpheios-word-usage__examples--sources-visible .alpheios-word-usage__examples-post {
  padding-bottom: 5px;
  border-bottom: none;
}
.alpheios-panel--compact .alpheios-word-usage__examples-source-link-large {
  display: none;
}